import axios from 'axios';
import { Product, ApiResponse, SearchFilters } from '../types';

const API_BASE_URL = 'http://localhost:8000';

const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Request interceptor for caching
const cache = new Map<string, { data: any; timestamp: number }>();
const CACHE_DURATION = 5 * 60 * 1000; // 5 minutes

api.interceptors.request.use((config) => {
  const cacheKey = `${config.method}-${config.url}-${JSON.stringify(config.params)}`;
  const cached = cache.get(cacheKey);
  
  if (cached && Date.now() - cached.timestamp < CACHE_DURATION) {
    return Promise.resolve({ ...config, data: cached.data } as any);
  }
  
  return config;
});

// Response interceptor for caching
api.interceptors.response.use((response) => {
  const cacheKey = `${response.config.method}-${response.config.url}-${JSON.stringify(response.config.params)}`;
  cache.set(cacheKey, { data: response.data, timestamp: Date.now() });
  return response;
});

export const productApi = {
  // Get products with pagination and filters
  getProducts: async (page = 1, limit = 12, filters?: SearchFilters): Promise<ApiResponse<Product[]>> => {
    const params = new URLSearchParams({
      page: page.toString(),
      limit: limit.toString(),
      ...(filters?.category && { category: filters.category }),
      ...(filters?.minPrice && { min_price: filters.minPrice.toString() }),
      ...(filters?.maxPrice && { max_price: filters.maxPrice.toString() }),
      ...(filters?.brand && { brand: filters.brand }),
      ...(filters?.rating && { rating: filters.rating.toString() }),
      ...(filters?.sortBy && { sort_by: filters.sortBy }),
      ...(filters?.sortOrder && { sort_order: filters.sortOrder }),
    });

    const response = await api.get(`/products?${params}`);
    return response.data;
  },

  // Get single product by ID
  getProduct: async (id: string): Promise<Product> => {
    const response = await api.get(`/products/${id}`);
    return response.data;
  },

  // Search products
  searchProducts: async (query: string, page = 1, limit = 12): Promise<ApiResponse<Product[]>> => {
    const params = new URLSearchParams({
      q: query,
      page: page.toString(),
      limit: limit.toString(),
    });

    const response = await api.get(`/products/search?${params}`);
    return response.data;
  },

  // Get categories
  getCategories: async (): Promise<string[]> => {
    const response = await api.get('/categories');
    return response.data;
  },

  // Get health stats
  getHealthStats: async () => {
    const response = await api.get('/health-stats');
    return response.data;
  },
};

export default api; 